.IR-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2% 10%;
    justify-content: space-between;
    align-items: flex-start;
    gap: 10px;
    height: 80vh;
}


.ir-section1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    padding-right: 5%;
    gap: 20px;
    flex: 1;
    height: 100%;
    color: #3d3b3b;
    overflow: hidden;
}

.irNameAndPriceWrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}

.itemNameWrapper {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 20px;

}

.itemNameWrapper button {
    border: none;
    display: flex;
    background: transparent;
    cursor: pointer;
    height: auto;
}

.itemName {
    flex: 1;
    display: flex;
    font-weight: bold;
    font-size: 12px;
    word-spacing: 5px;
}

.itemPriceWrapper {
    width: 100%;
    display: flex;
    font-size: 15px;
    letter-spacing: 2px;
}

.itemPrice {
    color: #3d3b3b;
    font-weight: bold;
}

.productInfo {
    display: flex;
    flex-direction: column;
    position: relative;
    height: auto;
}

.productDetails {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    gap: 10px;
    height: 30px;
    width: 100%;
}

.productDetails button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: none;
    width: 100%;
    cursor: pointer;
}


.productDetailsHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.productDetails-info-writeup-wrapper {
    position: relative;
    height: 100%;
    width: 100%;
    /* overflow: hidden; */
}


.productDetailInformation {
    position: relative;
    display: flex;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
}

.productDetailInformation p {
    display: flex;
    background-color: green;
    font-size: 12px;
    padding-right: 10px;
}

.productShippingAndReturnsPolicy {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background-color: #f8f8f8;
    transform: translateY(0%);
    transition: all 350ms ease-in-out;
    border-top: 1px solid #3d3b3b;
}

.productShippingAndReturnsPolicyTransform {
    transform: translateY(-100%);
}

.productShippingAndReturnsPolicy button {
    justify-content: center;
    background: transparent;
    align-items: center;
    cursor: pointer;
    display: flex;
    border: none;
    width: 100%;
}

.shippingAndReturnContainer {
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f8f8f8;
}

.productInfo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
}

.productInfo p {
    background-color: #f8f8f8;
    font-size: 15px;
    display: flex;

}

.productInfo ul {
    display: flex;
    flex-direction: column;
    background-color: #f8f8f8;
    gap: 10px;
    position: relative;
    overflow: hidden;
}

.productInfo ul li {
    font-size: 12px;
    display: flex;
}

.ulOverlay {
    background-color: #f8f8f8;
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    transform: translateY(0%);
    border-top: 1px solid #3d3b3b;
    transition: all 350ms ease-in-out;
}


.ulOverlayTransform {
    transform: translateY(100%);
}


/* ____________________________________________ */

.ir-section2 {
    display: flex;
    flex: 3;
    height: 100%;
    justify-content: space-between;
    align-items: flex-start;
}

.ir-productImageWrapper {
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    flex: 2;
    overflow: hidden;
    display: flex;
    background-color: #f8f8f8;

}

.ir-productImageWrapper-container {
    padding: 30px;
    height: 100%;
    width: 80%;
    display: flex;
    background-color: white;

}

.ir-productImageWrapper img {
    display: flex;
    margin: auto;
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.ir-ProductColorPallet {
    height: auto;
    width: auto;
    flex: 1;
    gap: 30PX;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.ir-productColorWrapper {
    display: flex;
    gap: 2px;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.ir-productColorContainer {
    display: flex;
    gap: 5px;
    justify-content: space-between;
    align-items: center;
}

.ir-productColorContainer button {
    background-color: pink;
    display: flex;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 8px;
}


.colorCheck {
    display: flex;
    height: 100%;
    width: 100%;
    color: black;
}

.ir-productSizeWrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
    justify-content: space-between;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
}

.ir-productSizeWrapperContainer {
    display: flex;
    width: 100%;
    height: auto;
    gap: 10px;
}

.ir-productSizeWrapper button {
    display: flex;
    position: relative;
    background: none;
    width: 25px;
    height: 25px;
    justify-content: center;
    align-items: center;
    border: 1px solid #3d3b3b;
    font-size: 15px;
    text-align: center;
    text-transform: uppercase;
}


.ir-productSizeChecker {
    display: flex;
    position: absolute;
    z-index: 1;
    margin: auto;
}

.ir-orderNumberBtnWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: auto;
}

.ir-orderNumberBtnWrapper p {
    background-color: #f8f8f8;
    color: rgb(57, 128, 156);
    width: 50px;
    display: flex;
    margin: 0px 20px;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.ir-orderNumberBtnWrapper button {
    height: 20px;
    width: 40px;
    background-color: #0f0f0f;
    border: none;
    color: white;
    font-size: 12px;
    cursor: pointer;
}

.ir-addToCartButton {
    width: 100%;
    height: auto;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: flex-start;
}

.ir-addToCartButton button {
    border: none;
    display: flex;
    color: white;
    cursor: pointer;
    padding: 10px 20px;
    background-color: #0f0f0f;
}

.it-mobile {
    display: none;
}

@media screen and (max-width:900px) {
    .it-web {
        display: none;
    }

    .it-mobile {
        display: flex;
    }

    .itemName {
        flex: 1;
        display: flex;
        font-weight: bold;
        font-size: 15px;
        word-spacing: 5px;
    }

    .IR-container {
        flex-direction: column-reverse;
        height: auto;
    }

    .ir-section1 {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        padding-right: unset;
        gap: 20px;
        flex: 1;
        height: auto;
        color: #3d3b3b;
        overflow: hidden;
        margin: auto;
    }

    .productInfo {
        display: flex;
        flex-direction: column;
        position: relative;
        height: auto;
    }

    .productDetails-info-writeup-wrapper {
        position: relative;
        height: 100%;
        width: 100%;
        /* overflow: hidden; */
    }

    .productDetailInformation {
        position: relative;
        display: flex;
        height: 300px;
        width: 100%;
        background-color: unset;
        overflow-y: scroll;
    }

    .shippingAndReturnContainer {

        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #f8f8f8;
    }

    .ir-section2 {
        flex-direction: column;
        gap: 40px;
    }

    .ir-productImageWrapper {
        background-color: red;
    }

    .ir-productImageWrapper {
        flex: 1;
    }

    .ir-productImageWrapper-container {
        padding: 30px;
        height: 100%;
        width: 100%;

    }

    .ir-ProductColorPallet {
        height: auto;
        width: 100%;
        flex: 1;
        gap: 20PX;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .ir-productColorWrapper {
        display: flex;
        gap: 10px;
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
    }

    .ir-productColorContainer {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 100%;
    }

    .ir-productSizeWrapperContainer {

        justify-content: space-evenly;
    }

    .ir-addToCartButton {
        width: 100%;
        height: auto;
        display: flex;
        position: relative;
        align-items: center;
        justify-content: flex-start;
    }

    .ir-addToCartButton button {
        border: none;
        display: flex;
        color: white;
        cursor: pointer;
        padding: 10px 20px;
        width: 80%;
        margin: auto;
        justify-content: center;
        background-color: #0f0f0f;
    }




}