.footerContainer {
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    padding: 5%;
    gap: 30px;
    z-index: 1;

    background-color: #f8f8f8;
}

.footerContainer hr {
    height: 1px;
    width: 100%;
    margin: auto;
    display: flex;
    border: none;
    background-color: #383838;
}

.footerContainer p {
    display: flex;
    font-size: 15px;
    color: #383838;
}

.footerSocialConnect {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    height: auto;
    z-index: 1;
}

.subscribeWrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 20px;
}

.footerSection1 {
    height: auto;
    width: 40%;
    gap: 10px;
    display: flex;
    flex-direction: column;

}

.footerSection1 p {
    color: gray;
    word-spacing: 5px;
    letter-spacing: 3px;
}

.ftr-inputWrapper {
    height: 50px;
    width: 100%;
    display: flex;
    border: 1px solid #363636;

}

.ftr-inputWrapper input {
    flex: 1;
    display: flex;
    border: none;
    outline: none;
    padding: 5px 10px;
    font-size: 20px;
    background: transparent;
}

.ftr-inputWrapper button {
    width: 50px;
    height: auto;
    border: none;
    display: flex;
    cursor: pointer;
    position: relative;
    align-items: center;
    background: transparent;
    justify-content: center;
}

.footerSection2 {
    display: flex;
    gap: 30px;


}

.socialBtn {
    display: block;
    background: transparent;
    border: none;
    font-size: 40px;
    cursor: pointer;
    transition: all 350ms ease-in-out;
}

.socialBtn:hover {
    transform: scale(1.2);
}

.footerShoppingCategoryLinks {
    display: flex;
    justify-content: space-EVENLY;
    align-items: flex-start;
    width: 100%;
    height: auto;
    z-index: 1;
}

.f-group {
    flex-direction: column;
    display: flex;
    gap: 6px;
}

.f-group h2 {
    text-transform: uppercase;
}

.f-group p {
    font-size: 12px;
}

.ftr-1,
.ftr-2,
.ftr-3 {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.footerWriteUp {
    display: flex;
    width: 100%;
    height: auto;
    justify-content: center;
    align-items: center;
}

.footerWriteUp p {
    font-size: 12px;
    text-transform: capitalize;
    word-spacing: 5px;
    text-align: center;
}

/* ___________________________ */


@media screen and (max-width:900px) {
    .footerContainer {
        flex-direction: column;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        padding: 5% 10%;
        gap: 20px;
        z-index: 1;
    }

    .footerSocialConnect {
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        gap: 20px;
    }

    .footerSection1 {
        height: auto;
        width: 100%;
        gap: 20px;
        display: flex;
        flex-direction: column;

    }

    .footerShoppingCategoryLinks {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;

        justify-content: space-between;
    }
}